/* Main image styling */
.main-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

/* Editable fields container */
.editable-fields {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  max-width: 1200px;
  width: 45%;
  margin: 0 auto;
}
.description-input .ql-container{
  height: 400px;
}
/* Title input styling */
.title-input {
  font-size: 2rem;
  font-weight: bold;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

/* Subtitle input styling */
.subtitle-input {
  font-size: 1.5rem;
  font-weight: 600;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

/* Description textarea styling */
.description-input {
  font-size: 1rem;
  line-height: 1.5;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  min-height: 400px;
  resize: vertical;
  width: 100%;
}

/* Save button styling */
.save-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #a10e2d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.save-button:hover {
  background-color: #a10e2d;
}
