.members-section {
  width: 100%;
  text-align: center;
}

.main-image {
  width: 100%;
  height: auto;
  max-height: 400px;
  object-fit: cover;
  margin-bottom: 20px;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.professors-list, .members-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.member-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 700px;
  padding: 20px;
  border-radius: 8px;
}

.member-name-input, .member-description-input {
  width: 100%;
  max-width: 600px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 10px;
}

.member-name-input {
  font-size: 1.2rem;
  font-weight: bold;
}

.member-description-input {
  font-size: 1rem;
  line-height: 1.5;
  resize: vertical;
}

.profile-image {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

.profile-image-input {
  margin-bottom: 10px;
}

.member-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.save-button, .delete-button {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
}

.save-button {
  background-color: #a10e2d;
  color: #fff;
}

.delete-button {
  background-color: #d9534f;
  color: #fff;
}

.save-button:hover {
  background-color: #800d26;
}

.delete-button:hover {
  background-color: #c9302c;
}

.view-more-button, .add-button {
  margin-top: 20px;
  padding: 10px 20px;
  background: #a10e2d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-button {
  align-self: center;
  margin-bottom: 20px;
}

.view-more-button:hover, .add-button:hover {
  background-color: #800d26;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .member-item {
    width: 90%;
  }

  .profile-image {
    width: 120px;
    height: 120px;
  }

  .main-image {
    max-height: 250px;
  }
}

@media (max-width: 480px) {
  .member-item {
    width: 100%;
  }

  .profile-image {
    width: 100px;
    height: 100px;
  }

  .main-image {
    max-height: 200px;
  }
}
