/* Main image styling */
.main-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

/* Section title styling */
.section-title {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
/* Research list container */
.research-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  padding-left: 0;
}

.research-description-editor .ql-editor {
  height: 300px; /* 에디터 부분만 원하는 높이로 설정 */
}

/* Research item styling */
.research-item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 80%;
  max-width: 800px;
  background-color: #fff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Input fields styling */
.research-title-input,
.research-brief-input,
.research-description-input {
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

.research-description-input {
  min-height: 100px;
  resize: vertical;
}

/* Image styling */
.research-image {
  height: auto;
  border-radius: 4px;
  margin-bottom: 10px;
}

/* File input styling */
.research-image-input {
  font-size: 1rem;
  padding: 5px;
}

/* Checkbox styling */
input[type="checkbox"] {
  transform: scale(1.5);
  margin-right: 10px;
}

/* Action buttons styling */
.research-actions {
  display: flex;
  justify-content: space-between;
}

.save-button,
.delete-button {
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #a10e2d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 48%;
}

.save-button:hover {
  background-color: #800d26;
}

/* Divider styling */
.research-divider {
  margin-top: 20px;
  border: none;
  height: 1px;
  background-color: #ddd;
}

/* Add button styling */
.add-button {
  font-size: 1rem;
  color: #fff;
  background-color: #a10e2d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 20px;
}

.add-button:hover {
  background-color: #a10e2d;
}

/* Responsive styling */
@media (max-width: 768px) {
  .section-title {
    font-size: 1.5rem;
  }

  .research-item {
    width: 90%;
  }

  .save-button,
  .delete-button {
    font-size: 0.9rem;
    padding: 8px 16px;
  }
}

@media (max-width: 480px) {
  .research-item {
    width: 100%;
  }

  .section-title {
    font-size: 1.2rem;
  }

  .save-button,
  .delete-button {
    font-size: 0.8rem;
    padding: 6px 12px;
  }
}
/* Publish.css */
.research-section {
  text-align: center;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.all-pub-list {
  width: 55%;
  margin: 0 auto;
}

.pub-item {
  display: flex;
  border: none;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
}
.admin-content .all-pub-list .pub-item{
  flex-direction: column;
  align-items: center;
}

.pub-item .pub-year {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.pub-year label {
  font-weight: bold;
  margin-right: 10px;
  min-width: 50px;
  text-align: right;
}

.pub-year input[type="text"] {
  width: 100px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.publication-editor {
  margin-top: 10px;
}

.pub-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 10px;
}

.save-button,
.delete-button {
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  background-color: #8b0c26;
  color: white;
  cursor: pointer;
}

.save-button:hover,
.delete-button:hover {
  background-color: #6f091e;
}

.add-button {
  margin-top: 20px;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  background-color: #8b0c26;
  color: white;
  cursor: pointer;
}

.add-button:hover {
  background-color: #6f091e;
}

