.research-section {
  text-align: center;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.research-list, .all-pub-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 70%;
  margin: 0 auto;
}

.research {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px;
  border-radius: 8px;
}

.research-name a {
  color: black;
  text-decoration: none;
}

.research-name a:hover {
  text-decoration: underline;
}

.research-image {
  width: 200px;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 20px;
}

.research-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

.research-name {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  margin: 0;
}

.research-description {
  font-size: 1rem;
  color: #666;
  margin-top: 10px;
}

.all-pub-list {
  margin-top: 20px;
}

.pub-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: #999 solid 0.0000000001px;
}

.pub-year {
  font-weight: bold;
  font-size: 1.2rem;
  width: 50px;
  text-align: right;
  margin-right: 20px;
  align-self: flex-start;
}

.pub-content {
  flex: 1;
  display: flex;
  border-left: lightgray solid 0.5px;
  padding-left: 10px;
}

.pub-index {
  font-weight: bold;
  margin-right: 5px;
}

.pub-title {
  color: #000;
  text-decoration: none;
  margin-top: 0px;
}

.pub-divider {
  width: 90%;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 10px 0;
}

@media (max-width: 768px) {
  .research-image {
    width: 150px;
    height: 225px;
    margin-right: 15px;
  }

  .section-title {
    font-size: 1.5rem;
  }

  .pub-year {
    font-size: 1rem;
    width: 15%;
  }

  .pub-content {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .research {
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  .research-image {
    width: 100px;
    height: 150px;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .research-info {
    text-align: center;
  }

  .research-name {
    font-size: 1.2rem;
  }

  .research-description {
    font-size: 0.9rem;
  }

  .pub-year {
    font-size: 0.9rem;
    text-align: center;
    width: 100%;
    margin-bottom: 5px;
  }

  .pub-content {
    text-align: center;
    font-size: 0.85rem;
  }
}
