/* Main image styling */
.main-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}
.publication-content-input .ql-container{
  height: 150px;
}

/* Section title */
.section-title {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

/* Editable fields container */
.publication-list {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
  list-style-type: none;
}

.publication-item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 30px;
  padding: 20px;
  
  background-color: white;
  
}

/* Title input styling */
.publication-title-input {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

/* Content textarea styling */
.publication-content-input {
  font-size: 1rem;
  line-height: 1.5;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  min-height: 200px;
  resize: vertical;
  width: 100%;
}

/* Publication image styling */
.publication-img {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 10px;
}

/* Save button styling */
.save-button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #a10e2d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.upload-container {
  text-align: center;
  margin-bottom: 20px;
}

.save-button:hover {
  background-color: #800d26;
}

.add-button {
  margin-top: 10px;
  width: 30%;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #a10e2d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-button:hover {
  background-color: #800d26;
}

.delete-button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #a10e2d;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.delete-button:hover {
  background-color: #800d26;
}

/* Divider between publications */
.publication-divider {
  margin-top: 20px;
  border: none;
  height: 1px;
  background-color: #ddd;
}
.delete-button{
  margin-left: 10px;
}